<template>
  <div class="contact" id="contact">
    <h2>联系方式</h2>
    <div class="box" v-for="(item, index) in arr">
        <p>
          {{ item.title }} : {{ item.detail }}
        </p>
    </div>
  </div>
</template>

<script>

  export default {
    data(){
      return{
        arr:[]
      }
    },
    mounted(){
      this.getData()
    },
    methods:{
      getData(){
        var _this = this;
        this.axios.get('./src/data/contact.data')
        .then(function(res){
          _this.arr = res.data
        })
        .catch(function(err){
          console.log(err)
        })
      }
    }
  }
</script>

<style scoped>
  .contact{
    width: 100%;
    height: 14rem;
    background: url('../assets/images/background/desktop.jpg') no-repeat;
    background-size: cover;
    text-align: center;
    color: #fff;
    font: bold  1.0rem/2.0rem 'Raleway-Regular', 'Microsoft Yahei',sans-serif;
    padding-top: 1.0rem;
  }
</style>



