<template>
  <div>
    <swiper :options="swiperOption" ref="mySwiperA">
      <swiper-slide>
        <img :src="imgSrc.dxxd" alt="东邪西毒">
      </swiper-slide>
      <swiper-slide>
        <img :src="imgSrc.afzz" alt="阿飞正传">
      </swiper-slide>
      <swiper-slide>
        <img :src="imgSrc.cqsl" alt="重庆森林">
      </swiper-slide>
      <swiper-slide>
        <img :src="imgSrc.hynh" alt="花样年华">
      </swiper-slide>
      <div class="swiper-pagination" slot="scrollbar"></div>
    </swiper>
  </div>
</template>

<script>

export default {
  name: 'carrousel',
  data() {
    return {
      imgSrc:{
        dxxd:'./src/assets/images/banner/dxxd.jpg',
        afzz:'./src/assets/images/banner/afzz.jpg',
        cqsl:'./src/assets/images/banner/cqsl.jpg',
        hynh:'./src/assets/images/banner/hynh.jpg'
      },
      swiperOption: {
        notNextTick: true,
        initialSlide: 0,
        deep: 500,
        loop: true,
        autoplay: 2000,
        direction : 'horizontal',
        grabCursor : true,
        setWrapperSize :true,
        autoHeight: null,
        pagination : '.swiper-pagination',
        paginationClickable :true,
        prevButton:'.swiper-button-prev',
        nextButton:'.swiper-button-next',
        scrollbar: null,
        mousewheelControl : true,
        observeParents:true,
        debugger: true,
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiperA.swiper
    }
  },
  mounted() {
    this.swiper.slideTo(1, 1000, false)
  }
}

</script>

<style scope>
.swiper-container {
  width: 100%;
  height: 15rem;
  margin-bottom: 0.1rem;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
.swiper-slide img {
  width: 100%;
}
</style>



