<template>
  <div id="resume" class="resume">
    <div class="avatar">
      <img :src="imgSrc" />
    </div>
    <div class="main">
      <h2>万昆 网页前端工程师</h2>
      <p>
        {{text}}
      </p>
      <div class="btn">
        <router-link to="/one" tag="div">
          <mu-flat-button label="Vue-Router" class="demo-flat-button" icon="ac_unit" color="#e8eaf6" />
        </router-link>
        <router-link to="/two" tag="div">
          <mu-flat-button label="Vuex" class="demo-flat-button" icon="ac_unit" color="#e8eaf6" />
        </router-link>
        <router-link to="/three" tag="div">
          <mu-flat-button label="other" class="demo-flat-button" icon="ac_unit" color="#e8eaf6" />
        </router-link>
      </div>
    </div>
    <transition name="move">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>
<script>

  export default {
    name: 'resume',
    data(){
      return {
        text:'熟练掌握HTML5、CSS3、JavaScript开发，熟悉W3C标准与ES规范，熟悉Web语义化，有良好的责任心和学习能力，乐于分享。',
        imgSrc:'./src/assets/images/Onizukatumblr.gif'
      }
    }
  }
</script>

<style scoped>
.move-enter-active {
  transition: all .3s ease;
}
.move-leave-active {
  transition: all .6s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.move-enter, .move-leave-active {
  transform: translateX(9px);
  opacity: 0;
}

  .btn{
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
  }
  .resume{
    width: 100%;
    min-height: 15rem;
    margin-bottom: 0.1rem;
    background: url('../assets/images/background/winter.jpg') no-repeat fixed center;
    background-size: cover;
    color: #fff;
  }
  .avatar{
    width: 4rem;
    height: 4rem;
    border-radius: 10%;
    margin: 0 auto;
  }
  .avatar img{
    width: 4rem;
    height: 4rem;
    margin-top: 0.2rem;
  }
  .main{
    width: 100%;
    height: auto;
    margin: 0 auto;
    text-align: center;
  }
  .main p{
    font-size: 14px;
  }
</style>



