<template>
  <div id="app">
    <bar v-show="showBar"></bar>
    <banner></banner>
    <resume></resume>
    <contact></contact>
  </div>
</template>

<script>
;(function(doc,win){
  if (!win.addEventListener) return;
  var html = doc.documentElement,
    resizeEvt = 'onorientationchange' in window ? 'onorientationchange' : 'resize',
    recalc = function(){
      html.style.fontSize = html.clientWidth/640*20 + 'px';
    };
  if(!doc.addEventListener) return;
  doc.addEventListener(resizeEvt, recalc, false);
  win.addEventListener('DOMContentLoaded', recalc, false);
})(document,window);

import Banner from './components/Banner.vue'
import Resume from './components/Resume.vue'
import Contact from './components/Contact.vue'
import Bar from './components/Bar.vue'
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'app',
  computed:mapGetters([
    'showBar'
  ]),
  watch:{
    $route(to){
      switch (to.path){
        case '/one': this.$store.dispatch('hideBar');
        break;
        case '/two': this.$store.dispatch('hideBar');
        break;
        case '/three': this.$store.dispatch('hideBar');
        break;
        default: this.$store.dispatch('showBar')
      }
    }
  },
  components:{
    Banner,
    Resume,
    Contact,
    Bar
  }
}
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
html{
  font-size: 20px;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>



