<template>
  <div id="three" class="three">
    {{text}}
  </div>
</template>

<script>

export default {
  name: 'three',
  data(){
    return {
      text: '利用axios获取的联系方式,基于Vue2.0的swiper和muse-ui.'
    }
  }
}
</script>

<style scoped>
.three{
  text-align: center;
  line-height: 15.0rem;
  width: 100%;
  height: 15.0rem;
}

</style>



